.card {
    display: flex;
    align-items: center;
    padding: 15px;
    margin: 15px;
    width: 35rem ;
    height: 8rem;
    border-radius: 8px;
    border: 1px solid #ddd;
  }

  .card-img {
    font-size: 24px;
    background: #F7FEE7;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-right: 10px;
  }
  
  .card-titulo {
    font-size: 20px;
    font-weight: bold;
    padding-bottom: 5px;
  }
  
  .card-descricao {
    font-size: 14px;
    color: #555;
  }
  
  .alinhamento{
    margin-top: 50px;
    justify-content: center;
  }